var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"page-container"},[_c('el-breadcrumb',[_c('el-breadcrumb-item',{attrs:{"to":{ path: '/' }}},[_vm._v("首頁")]),_vm._v(" "),_c('el-breadcrumb-item',[_vm._v("服務項目")]),_vm._v(" "),_c('el-breadcrumb-item',[_vm._v("美膚光療")]),_vm._v(" "),_c('el-breadcrumb-item',[_vm._v("極限音波拉提")])],1),_vm._ssrNode(" "),_c('Title',{attrs:{"title":"極限音波拉提","titleEn":"ULTHERAPY"}}),_vm._ssrNode(" <div class=\"article-container service-page\"><picture><source"+(_vm._ssrAttr("srcset",require("assets/img/service/preventiveMedicine/ultherapy/banner.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/service/preventiveMedicine/ultherapy/banner.jpg")))+" alt=\"極限音波拉提 Banner\"></picture> <h2>高強度聚焦式超音波 刺激膠原蛋白的增生與重組</h2> <p>以高強度聚焦式超音波 (High Intensity Focused Ultrasound; HIFU)，針對傳統手術拉皮治療的SMAS層(全名為Superficial Musculo- Aponeurotic System)(表淺肌肉建膜系統，俗稱筋膜層) 利用深度1.5mm~4.5mm聚焦的能量變成微細的「熱點」，在皮下作用的溫度可到達65℃~70℃，會激發身體的自我更新機制，刺激膠原蛋白增生，達到緊緻、拉提等效果。</p></div> <div class=\"article-container\"><h2>不動刀！也能擁有緊緻美肌</h2> <p>超音波拉提擁有「即時影像（Real Time）科技」，可以由螢幕上方畫面直接看到皮膚內部狀態，清楚了解目前擊發深度位置。透過「定位指標線」，精準治療深度以及能量控制，讓能量落點更準確，緊實度與拉提性才能確保，更使安全升級。可以觀測深達8mm的組織層，當然也涵蓋SMAS層。因為治療能量、參數設定都可以透過這樣直接觀察皮膚狀態後設定或是更改，有助於醫師在治療過程，能更加得心應手地運用能量。</p></div> <div class=\"article-container\"><h2>多層次精準定位治療</h2> <iframe src=\"https://www.youtube.com/embed/q7mVLHwiAlc\" title=\"YouTube video player\" frameborder=\"0\" allow=\"accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture\" allowfullscreen=\"allowfullscreen\"></iframe> <p>專業探頭多重選擇：不同治療部位採用不同探頭，Transducers探頭具有三種深度：</p> <ol><li>針對真皮淺層→1.5mm</li> <li>針對皮下脂肪→3.0mm</li> <li>針對SMAS層、頸闊肌→4.5mm</li></ol> <picture><source"+(_vm._ssrAttr("srcset",require("assets/img/service/preventiveMedicine/ultherapy/1.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/service/preventiveMedicine/ultherapy/1.jpg")))+" alt=\"多層次精準定位治療\"></picture></div> <div class=\"article-container\"><h2>療程特性</h2> <h3>⦿ 非侵入式、零傷口、零恢復期</h3> <p>Ulthera超音波拉提並不會在皮層表面造成傷口，因此治療完後您可立即恢復日常生活作息。</p> <h3>⦿ 100%掌握治療位置，安全看得見</h3> <p>即時(real time)顯影+專利定位指標線，治療中可即時觀察、調整能量落點於組織中的位置，精準掌握效果及安全性。</p> <h3>⦿ 皮下4.5MM 模擬手術SMAS治療概念</h3> <p>治療能量深度直達SMAS(表淺肌肉建膜系統，俗稱筋膜層)SMAS為以往只有手術拉皮時才能治療到深度，因此可達到拉提效果。</p></div> <div class=\"article-container\"><h2>療程治療區域</h2> <picture><source"+(_vm._ssrAttr("srcset",require("assets/img/service/preventiveMedicine/ultherapy/2.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/service/preventiveMedicine/ultherapy/2.jpg")))+" alt=\"多層次精準定位治療\"></picture></div> <div class=\"article-container\"><h2>美麗案例</h2> <picture><source"+(_vm._ssrAttr("srcset",require("assets/img/service/preventiveMedicine/ultherapy/案例1.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/service/preventiveMedicine/ultherapy/案例1.png")))+" alt=\"案例1\"></picture> <picture><source"+(_vm._ssrAttr("srcset",require("assets/img/service/preventiveMedicine/ultherapy/案例2.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/service/preventiveMedicine/ultherapy/案例2.png")))+" alt=\"案例2\"></picture> <picture><source"+(_vm._ssrAttr("srcset",require("assets/img/service/preventiveMedicine/ultherapy/案例3.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/service/preventiveMedicine/ultherapy/案例3.png")))+" alt=\"案例3\"></picture> <picture><source"+(_vm._ssrAttr("srcset",require("assets/img/service/preventiveMedicine/ultherapy/案例4.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/service/preventiveMedicine/ultherapy/案例4.png")))+" alt=\"案例4\"></picture> <p>療程效果會因個人體質而有所差異</p></div> <div class=\"article-container\"><h2>術前小提醒</h2> <ol><li>- 曾經做過多次煥膚者（視情況）、嚴重異位性皮膚炎、免疫系統異常不建議進行治療。</li> <li>- 嚴重化膿的皮膚或嚴重青春痘的皮膚、特殊皮膚情況者…等等不建議進行治療。</li> <li>- 曾經使用其他雷射後嚴重反黑者不建議進行治療。</li> <li>- 有心臟性疾病、裝置有心律調整器及孕婦不建議進行治療。</li> <li>- 一個月內有做微整形或使用換膚產品者，請事先告知醫師。</li> <li>- 治療範圍曾注射人工填充物，如：矽膠，不建議進行治療。</li> <li>- 治療時身上嚴禁佩戴任何金屬物品。</li> <li>- 請務必告知醫護人員、治療範圍是否曾經動過手術。</li> <li>- 裝置子宮避孕器、施打過肉毒桿菌並不會影響音波拉皮的治療效果。</li></ol></div> <div class=\"article-container\"><h2>術後小叮嚀</h2> <ol><li>治療後不須冰敷，可使用保養品，並加強保濕及防曬，彩妝品視皮膚情況使用。</li> <li>治療後一周內請勿泡溫泉、游泳、洗三溫暖、蒸氣或烤箱（避免身體水份過度流失及皮膚過度乾燥。</li> <li>治療後不會造成傷口，若有溫熱感會於數小時內消失，少數人治療後有輕微腫脹情形，3-5天甚至可能2-3星期才會消退。</li> <li>術後一周內禁止使用磨砂膏、A酸、果酸、美白產品或高單位濃度的藥品及保養品，若有特殊使用產品需先告知醫師或醫護人員。</li> <li>治療後一至二周為修護的黃金週期，故術後當天請開始加強保濕及防曬，療效會有加倍功效。</li> <li>治療部位大約1～2周感覺到有的緊緻感，約3個月後緊緻拉提的效果慢慢出現，且越來越明顯。</li> <li>「音波拉皮」的效果依施打的部位及個人的的保養方式不同，大約可以維持一至二年，經過多次的治療，可以達到更加的效果，建議半年~一年後可以進行治療。</li> <li><h4>本療程的效果可能因個人體質的不同而有差異，請務必與醫師溝通諮詢</h4></li></ol></div> <div class=\"article-container\"><h2>常見問題</h2> <h3>Q. 音波拉提是什麼</h3> <p>是一種無需開刀手術的超音波治療，利用肌膚自然修復反應，拉緊皮下筋膜層，協同膠原蛋白效果。</p> <h3>Q. 效果可以維持多久</h3> <p>超音波拉提啟動自身的修復機制，延緩肌膚老化，讓皮膚緊緻平滑，治療效果約可持續兩年，但也會因個人體質與生活習慣不同而有所差異。</p> <h3>Q. 有任何的副作用嗎</h3> <p>治療後的幾小時內可能會有些微的泛紅，幾天出現輕微水腫、小刺痛或觸痛感，也有可能會出現術後小區域瘀青或麻木感，但這都是少見且暫時性的。</p> <h3>Q. 治療過程中感覺如何</h3> <p>治療中傳輸能量時可能會有些不適，但這只是暫時的，也代表著新生的機制已經展開。醫生會提出符合您需要的治療方式。</p> <h3>Q. 超音波拉提只做臉部的拉提</h3> <p>除了臉部之外，SMAS也延續至頸部組織，故Ulthera治療亦對鬆弛的緊緻平滑，治療效果約可持續兩年，但也會因個人體質與生活習慣不同而有所差異。。</p></div>")],2)}
